.chat-pane {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: var(--vscode-editor-background);
  border-right: 1px solid var(--vscode-panel-border);
}

.chat-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  border-bottom: 1px solid var(--vscode-panel-border);
  background-color: var(--vscode-sideBar-background);
}

.chat-title {
  display: flex;
  align-items: center;
  gap: 12px;
}

.chat-title h3 {
  margin: 0;
  color: var(--vscode-sideBarTitle-foreground);
  font-size: 14px;
  font-weight: 600;
}

.model-selector {
  position: relative;
}

.current-model {
  background: var(--vscode-button-background);
  color: var(--vscode-button-foreground);
  border: none;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.current-model:hover {
  background: var(--vscode-button-hoverBackground);
}

.model-dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  background: var(--vscode-dropdown-background);
  border: 1px solid var(--vscode-dropdown-border);
  border-radius: 4px;
  min-width: 120px;
  z-index: 1000;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.model-option {
  width: 100%;
  padding: 8px 12px;
  background: none;
  border: none;
  color: var(--vscode-dropdown-foreground);
  font-size: 12px;
  cursor: pointer;
  text-align: left;
  transition: background-color 0.2s;
}

.model-option:hover {
  background: var(--vscode-list-hoverBackground);
}

.model-option.active {
  background: var(--vscode-list-activeSelectionBackground);
  color: var(--vscode-list-activeSelectionForeground);
}

.chat-actions {
  display: flex;
  gap: 8px;
}

.clear-btn {
  background: none;
  border: none;
  color: var(--vscode-foreground);
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  font-size: 14px;
  opacity: 0.7;
  transition: opacity 0.2s, background-color 0.2s;
}

.clear-btn:hover {
  opacity: 1;
  background: var(--vscode-toolbar-hoverBackground);
}

.messages-container {
  flex: 1;
  overflow-y: auto;
  padding: 0;
}

.empty-state {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 24px;
}

.welcome-message {
  text-align: center;
  max-width: 400px;
}

.welcome-message h4 {
  color: var(--vscode-foreground);
  margin: 0 0 12px 0;
  font-size: 16px;
}

.welcome-message p {
  color: var(--vscode-descriptionForeground);
  margin: 0 0 20px 0;
  line-height: 1.5;
}

.example-prompts {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.example-prompts button {
  background: var(--vscode-button-secondaryBackground);
  color: var(--vscode-button-secondaryForeground);
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  transition: background-color 0.2s;
}

.example-prompts button:hover {
  background: var(--vscode-button-secondaryHoverBackground);
}

.messages-list {
  padding: 0;
}

.message {
  padding: 16px;
  border-bottom: 1px solid var(--vscode-panel-border);
}

.message.user {
  background: var(--vscode-editor-background);
}

.message.assistant {
  background: var(--vscode-sideBar-background);
}

.message.system {
  background: var(--vscode-notifications-background);
}

.message.processing {
  opacity: 0.8;
}

.message-header {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
}

.message-role {
  font-size: 12px;
  font-weight: 600;
  color: var(--vscode-foreground);
  text-transform: capitalize;
}

.message-time {
  font-size: 11px;
  color: var(--vscode-descriptionForeground);
  margin-left: auto;
}

.regenerate-btn {
  background: none;
  border: none;
  color: var(--vscode-foreground);
  cursor: pointer;
  padding: 2px 4px;
  border-radius: 2px;
  font-size: 12px;
  opacity: 0.5;
  transition: opacity 0.2s, background-color 0.2s;
}

.regenerate-btn:hover {
  opacity: 1;
  background: var(--vscode-toolbar-hoverBackground);
}

.processing-indicator {
  font-size: 11px;
  color: var(--vscode-descriptionForeground);
  font-style: italic;
}

.message-content {
  margin: 0;
}

.message-content pre {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
  color: var(--vscode-foreground);
  font-family: var(--vscode-editor-font-family);
  font-size: 13px;
  line-height: 1.5;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.typing-animation {
  display: flex;
  gap: 4px;
  padding: 8px 0;
}

.typing-animation span {
  width: 6px;
  height: 6px;
  background: var(--vscode-foreground);
  border-radius: 50%;
  opacity: 0.4;
  animation: typing 1.4s infinite ease-in-out;
}

.typing-animation span:nth-child(1) {
  animation-delay: -0.32s;
}

.typing-animation span:nth-child(2) {
  animation-delay: -0.16s;
}

@keyframes typing {
  0%, 80%, 100% {
    opacity: 0.4;
    transform: scale(1);
  }
  40% {
    opacity: 1;
    transform: scale(1.1);
  }
}

/* Action Plan Styles */
.action-plan {
  margin-top: 16px;
  border: 1px solid var(--vscode-panel-border);
  border-radius: 6px;
  background: var(--vscode-editor-background);
}

.plan-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  border-bottom: 1px solid var(--vscode-panel-border);
  background: var(--vscode-sideBar-background);
}

.plan-header h4 {
  margin: 0;
  font-size: 14px;
  color: var(--vscode-foreground);
}

.risk-badge {
  padding: 2px 8px;
  border-radius: 12px;
  font-size: 10px;
  font-weight: 600;
  color: white;
  text-transform: uppercase;
}

.plan-summary {
  padding: 12px 16px;
  border-bottom: 1px solid var(--vscode-panel-border);
}

.plan-summary p {
  margin: 0 0 8px 0;
  color: var(--vscode-foreground);
  font-size: 13px;
  line-height: 1.4;
}

.plan-stats {
  display: flex;
  gap: 16px;
}

.plan-stats span {
  font-size: 11px;
  color: var(--vscode-descriptionForeground);
}

.actions-list {
  padding: 8px;
}

.action-item {
  margin-bottom: 8px;
  padding: 12px;
  border: 1px solid var(--vscode-panel-border);
  border-radius: 4px;
  background: var(--vscode-editor-background);
}

.action-item.approved {
  border-color: #28a745;
  background: rgba(40, 167, 69, 0.1);
}

.action-item.rejected {
  border-color: #dc3545;
  background: rgba(220, 53, 69, 0.1);
}

.action-item.pending {
  border-color: #ffc107;
  background: rgba(255, 193, 7, 0.1);
}

.action-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.action-type {
  font-size: 11px;
  font-weight: 600;
  color: var(--vscode-foreground);
  text-transform: uppercase;
  background: var(--vscode-badge-background);
  color: var(--vscode-badge-foreground);
  padding: 2px 6px;
  border-radius: 3px;
}

.action-risk {
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
}

.action-description {
  margin-bottom: 8px;
  font-size: 13px;
  color: var(--vscode-foreground);
  line-height: 1.4;
}

.action-preview {
  margin-bottom: 12px;
  padding: 8px;
  background: var(--vscode-textCodeBlock-background);
  border: 1px solid var(--vscode-panel-border);
  border-radius: 3px;
}

.action-preview code {
  font-family: var(--vscode-editor-font-family);
  font-size: 12px;
  color: var(--vscode-textPreformat-foreground);
}

.action-buttons {
  display: flex;
  gap: 8px;
}

.approve-btn {
  background: #28a745;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 12px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.approve-btn:hover {
  background: #218838;
}

.reject-btn {
  background: #dc3545;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 12px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.reject-btn:hover {
  background: #c82333;
}

.action-status {
  font-size: 12px;
  font-weight: 600;
  padding: 4px 8px;
  border-radius: 4px;
  text-align: center;
}

.action-status.approved {
  background: rgba(40, 167, 69, 0.2);
  color: #28a745;
}

.action-status.rejected {
  background: rgba(220, 53, 69, 0.2);
  color: #dc3545;
}

.action-status.executed {
  background: rgba(23, 162, 184, 0.2);
  color: #17a2b8;
}

.plan-actions {
  padding: 12px 16px;
  border-top: 1px solid var(--vscode-panel-border);
  text-align: center;
}

.execute-plan-btn {
  background: var(--vscode-button-background);
  color: var(--vscode-button-foreground);
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s;
}

.execute-plan-btn:hover {
  background: var(--vscode-button-hoverBackground);
}

/* Chat Input */
.chat-input {
  border-top: 1px solid var(--vscode-panel-border);
  background: var(--vscode-sideBar-background);
  padding: 12px 16px;
}

.input-container {
  display: flex;
  gap: 8px;
  align-items: flex-end;
}

.input-container textarea {
  flex: 1;
  background: var(--vscode-input-background);
  color: var(--vscode-input-foreground);
  border: 1px solid var(--vscode-input-border);
  border-radius: 4px;
  padding: 8px 12px;
  font-family: var(--vscode-font-family);
  font-size: 13px;
  resize: none;
  outline: none;
  min-height: 20px;
  max-height: 100px;
}

.input-container textarea:focus {
  border-color: var(--vscode-focusBorder);
}

.input-container textarea:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.send-btn {
  background: var(--vscode-button-background);
  color: var(--vscode-button-foreground);
  border: none;
  padding: 8px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  min-width: 40px;
  transition: background-color 0.2s;
}

.send-btn:hover:not(:disabled) {
  background: var(--vscode-button-hoverBackground);
}

.send-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
