___CSS_LOADER_EXPORT___.push([module.id, `/* VS Code-inspired Layout CSS */
.vsembed-app {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  background-color: var(--vscode-editor-background);
  color: var(--vscode-editor-foreground);
  font-family: var(--vscode-font-family);
  font-size: var(--vscode-font-size);
  overflow: hidden;
}

/* Main content area (below menu bar) */
.vsembed-main {
  flex: 1;
  display: flex;
  overflow: hidden;
}

/* Sidebar */
.vsembed-sidebar {
  width: 240px;
  min-width: 200px;
  max-width: 400px;
  background-color: var(--vscode-sideBar-background);
  border-right: 1px solid var(--vscode-sideBar-border);
  display: flex;
  flex-direction: column;
  resize: horizontal;
  overflow: hidden;
}

/* Content area (editor + preview + chat) */
.vsembed-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* Main panels area */
.vsembed-panels {
  flex: 1;
  display: flex;
  overflow: hidden;
}

/* Editor area */
.vsembed-editor-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  border-right: 1px solid var(--vscode-panel-border);
  overflow: hidden;
}

/* AI Chat panel */
.vsembed-chat-panel {
  width: 320px;
  min-width: 250px;
  max-width: 500px;
  background-color: var(--vscode-sideBar-background);
  border-left: 1px solid var(--vscode-panel-border);
  display: flex;
  flex-direction: column;
  resize: horizontal;
  overflow: hidden;
}

/* Bottom panel (terminal) */
.vsembed-bottom-panel {
  height: 250px;
  min-height: 100px;
  max-height: 400px;
  background-color: var(--vscode-panel-background);
  border-top: 1px solid var(--vscode-panel-border);
  display: flex;
  flex-direction: column;
  resize: vertical;
  overflow: hidden;
}

/* Panel headers */
.vsembed-panel-header {
  height: 32px;
  min-height: 32px;
  background-color: var(--vscode-tab-inactiveBackground);
  border-bottom: 1px solid var(--vscode-panel-border);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 12px;
  font-size: 12px;
  font-weight: 500;
  color: var(--vscode-tab-inactiveForeground);
  user-select: none;
}

.vsembed-panel-header span {
  flex: 1;
}

/* Panel content */
.vsembed-panel-content {
  flex: 1;
  overflow: auto;
  padding: 8px;
  background-color: var(--vscode-editor-background);
}

/* Utility classes */
.vsembed-flex {
  display: flex;
  align-items: center;
}

.vsembed-gap {
  gap: 4px;
}

/* Button styles */
.vsembed-btn-secondary {
  background: transparent;
  border: none;
  color: var(--vscode-tab-inactiveForeground);
  padding: 4px 6px;
  border-radius: 3px;
  font-size: 11px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.vsembed-btn-secondary:hover {
  background-color: var(--vscode-toolbar-hoverBackground);
  color: var(--vscode-foreground);
}

.vsembed-btn-secondary:active {
  background-color: var(--vscode-button-background);
}

/* Scrollbars */
.vsembed-app ::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}

.vsembed-app ::-webkit-scrollbar-track {
  background: var(--vscode-scrollbar-shadow);
}

.vsembed-app ::-webkit-scrollbar-thumb {
  background: var(--vscode-scrollbarSlider-background);
  border-radius: 6px;
}

.vsembed-app ::-webkit-scrollbar-thumb:hover {
  background: var(--vscode-scrollbarSlider-hoverBackground);
}

/* Resize handles */
.vsembed-sidebar:hover {
  resize: horizontal;
}

.vsembed-chat-panel:hover {
  resize: horizontal;
}

.vsembed-bottom-panel:hover {
  resize: vertical;
}

/* Focus styles */
.vsembed-panel-content:focus-within {
  outline: 1px solid var(--vscode-focusBorder);
  outline-offset: -1px;
}

/* Responsive design */
@media (max-width: 768px) {
  .vsembed-sidebar {
    width: 200px;
    min-width: 150px;
  }
  
  .vsembed-chat-panel {
    width: 280px;
    min-width: 200px;
  }
  
  .vsembed-bottom-panel {
    height: 200px;
    min-height: 80px;
  }
}

@media (max-width: 480px) {
  .vsembed-panels {
    flex-direction: column;
  }
  
  .vsembed-chat-panel {
    width: 100%;
    height: 200px;
    border-left: none;
    border-top: 1px solid var(--vscode-panel-border);
  }
}

/* Animation for panel transitions */
.vsembed-sidebar,
.vsembed-chat-panel,
.vsembed-bottom-panel {
  transition: all 0.2s ease-in-out;
}

/* Panel shadows for depth */
.vsembed-chat-panel,
.vsembed-bottom-panel {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .vsembed-sidebar,
  .vsembed-chat-panel,
  .vsembed-bottom-panel {
    border-width: 2px;
  }
}
