.menu-bar {
  display: flex;
  background-color: var(--vscode-menubar-background);
  color: var(--vscode-menubar-foreground);
  border-bottom: 1px solid var(--vscode-menubar-border);
  height: 30px;
  padding: 0 8px;
  user-select: none;
  font-size: 13px;
}

.menu-button {
  position: relative;
  display: flex;
  align-items: center;
  padding: 0 12px;
  cursor: pointer;
  transition: background-color 0.2s, color 0.2s;
  border-radius: 0;
}

.menu-button:hover,
.menu-button.active {
  background-color: var(--vscode-menubar-selectionBackground);
  color: var(--vscode-menubar-selectionForeground);
}

.menu-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  background: var(--vscode-menu-background);
  border: 1px solid var(--vscode-menu-border);
  border-radius: 0 0 4px 4px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  min-width: 200px;
  overflow: hidden;
}

.menu-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  cursor: pointer;
  font-size: 13px;
  color: var(--vscode-menu-foreground);
  transition: background-color 0.2s, color 0.2s;
  white-space: nowrap;
}

.menu-item:hover:not(.disabled) {
  background-color: var(--vscode-menu-selectionBackground);
  color: var(--vscode-menu-selectionForeground);
}

.menu-item.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.menu-icon {
  font-size: 14px;
  width: 16px;
  text-align: center;
  flex-shrink: 0;
}

.menu-label {
  flex: 1;
}

.menu-shortcut {
  font-size: 11px;
  color: var(--vscode-descriptionForeground);
  font-family: var(--vscode-editor-font-family);
  margin-left: auto;
}

.menu-arrow {
  font-size: 10px;
  margin-left: auto;
}

.menu-separator {
  height: 1px;
  background: var(--vscode-menu-separatorBackground);
  margin: 4px 8px;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
}

.modal {
  background: var(--vscode-dropdown-background);
  border: 1px solid var(--vscode-dropdown-border);
  border-radius: 6px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}

.modal.about-modal,
.modal.settings-modal {
  min-width: 500px;
  max-width: 90vw;
  max-height: 90vh;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 1px solid var(--vscode-dropdown-border);
  background: var(--vscode-sideBarSectionHeader-background);
}

.modal-header h2 {
  margin: 0;
  font-size: 18px;
  color: var(--vscode-foreground);
  font-weight: 600;
}

.modal-close {
  background: none;
  border: none;
  color: var(--vscode-foreground);
  cursor: pointer;
  font-size: 20px;
  line-height: 1;
  padding: 4px;
  border-radius: 3px;
  transition: background-color 0.2s;
}

.modal-close:hover {
  background: var(--vscode-toolbar-hoverBackground);
}

.modal-content {
  padding: 24px;
  max-height: 60vh;
  overflow-y: auto;
}

.modal-content::-webkit-scrollbar {
  width: 8px;
}

.modal-content::-webkit-scrollbar-track {
  background: var(--vscode-dropdown-background);
}

.modal-content::-webkit-scrollbar-thumb {
  background: var(--vscode-scrollbarSlider-background);
  border-radius: 4px;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  padding: 16px 24px;
  border-top: 1px solid var(--vscode-dropdown-border);
  background: var(--vscode-sideBarSectionHeader-background);
}

.btn-primary,
.btn-secondary {
  padding: 8px 20px;
  border: none;
  border-radius: 4px;
  font-size: 13px;
  cursor: pointer;
  transition: background-color 0.2s;
  font-weight: 500;
}

.btn-primary {
  background: var(--vscode-button-background);
  color: var(--vscode-button-foreground);
}

.btn-primary:hover {
  background: var(--vscode-button-hoverBackground);
}

.btn-secondary {
  background: var(--vscode-button-secondaryBackground);
  color: var(--vscode-button-secondaryForeground);
}

.btn-secondary:hover {
  background: var(--vscode-button-secondaryHoverBackground);
}

/* About Modal Specific Styles */
.about-content {
  text-align: center;
  color: var(--vscode-foreground);
}

.app-icon {
  font-size: 48px;
  margin-bottom: 16px;
}

.about-content h3 {
  margin: 0 0 8px 0;
  font-size: 24px;
  font-weight: 600;
  color: var(--vscode-foreground);
}

.version {
  margin: 0 0 16px 0;
  font-size: 14px;
  color: var(--vscode-descriptionForeground);
  font-family: var(--vscode-editor-font-family);
}

.description {
  margin: 0 0 24px 0;
  line-height: 1.6;
  color: var(--vscode-foreground);
  text-align: left;
}

.features {
  text-align: left;
}

.features h4 {
  margin: 0 0 12px 0;
  font-size: 16px;
  color: var(--vscode-foreground);
}

.features ul {
  margin: 0;
  padding-left: 20px;
  color: var(--vscode-foreground);
}

.features li {
  margin: 8px 0;
  line-height: 1.4;
}

/* Settings Modal Specific Styles */
.settings-content {
  color: var(--vscode-foreground);
}

.setting-group {
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--vscode-panel-border);
}

.setting-group:last-child {
  border-bottom: none;
  margin-bottom: 0;
}

.setting-group h4 {
  margin: 0 0 16px 0;
  font-size: 14px;
  font-weight: 600;
  color: var(--vscode-foreground);
}

.setting-group label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 12px;
  font-size: 13px;
  color: var(--vscode-foreground);
  cursor: pointer;
}

.setting-group input[type="checkbox"] {
  margin-left: 8px;
  cursor: pointer;
}

.setting-group select {
  background: var(--vscode-dropdown-background);
  color: var(--vscode-dropdown-foreground);
  border: 1px solid var(--vscode-dropdown-border);
  border-radius: 3px;
  padding: 4px 8px;
  font-size: 12px;
  cursor: pointer;
  margin-left: 8px;
  min-width: 100px;
}

.setting-group select:focus {
  outline: 1px solid var(--vscode-focusBorder);
  outline-offset: -1px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .menu-bar {
    height: 28px;
    font-size: 12px;
    padding: 0 4px;
  }

  .menu-button {
    padding: 0 8px;
  }

  .menu-dropdown {
    min-width: 160px;
  }

  .menu-item {
    padding: 6px 12px;
    font-size: 12px;
  }

  .menu-shortcut {
    display: none;
  }

  .modal.about-modal,
  .modal.settings-modal {
    min-width: 320px;
    margin: 20px;
  }

  .modal-header {
    padding: 16px 20px;
  }

  .modal-header h2 {
    font-size: 16px;
  }

  .modal-content {
    padding: 20px;
  }

  .modal-footer {
    padding: 12px 20px;
    gap: 8px;
  }

  .btn-primary,
  .btn-secondary {
    padding: 6px 16px;
    font-size: 12px;
  }

  .about-content h3 {
    font-size: 20px;
  }

  .app-icon {
    font-size: 36px;
    margin-bottom: 12px;
  }

  .setting-group label {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }

  .setting-group input[type="checkbox"],
  .setting-group select {
    margin-left: 0;
    align-self: flex-end;
  }
}

/* High Contrast Mode */
@media (prefers-contrast: high) {
  .menu-bar {
    border-bottom: 2px solid var(--vscode-menubar-border);
  }

  .menu-button:hover,
  .menu-button.active {
    border: 1px solid var(--vscode-focusBorder);
  }

  .menu-dropdown {
    border: 2px solid var(--vscode-menu-border);
  }

  .modal {
    border: 2px solid var(--vscode-dropdown-border);
  }
}

/* Animation */
.menu-dropdown {
  animation: menuFadeIn 0.15s ease-out;
}

@keyframes menuFadeIn {
  from {
    opacity: 0;
    transform: translateY(-4px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.modal {
  animation: modalFadeIn 0.2s ease-out;
}

@keyframes modalFadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Focus styles for accessibility */
.menu-button:focus,
.menu-item:focus {
  outline: 1px solid var(--vscode-focusBorder);
  outline-offset: -1px;
}

.modal-close:focus,
.btn-primary:focus,
.btn-secondary:focus {
  outline: 1px solid var(--vscode-focusBorder);
  outline-offset: 2px;
}
