.terminal-pane {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: var(--vscode-terminal-background);
  color: var(--vscode-terminal-foreground);
  font-family: var(--vscode-editor-font-family);
}

.terminal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--vscode-panel-background);
  border-bottom: 1px solid var(--vscode-panel-border);
  min-height: 35px;
}

.terminal-tabs {
  display: flex;
  flex: 1;
  overflow-x: auto;
}

.terminal-tabs::-webkit-scrollbar {
  height: 3px;
}

.terminal-tabs::-webkit-scrollbar-track {
  background: var(--vscode-panel-background);
}

.terminal-tabs::-webkit-scrollbar-thumb {
  background: var(--vscode-scrollbarSlider-background);
  border-radius: 3px;
}

.terminal-tab {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  background-color: var(--vscode-tab-inactiveBackground);
  color: var(--vscode-tab-inactiveForeground);
  border-right: 1px solid var(--vscode-tab-border);
  cursor: pointer;
  min-width: 100px;
  transition: background-color 0.2s, color 0.2s;
  user-select: none;
  white-space: nowrap;
}

.terminal-tab:hover {
  background-color: var(--vscode-tab-hoverBackground);
  color: var(--vscode-tab-hoverForeground);
}

.terminal-tab.active {
  background-color: var(--vscode-tab-activeBackground);
  color: var(--vscode-tab-activeForeground);
  border-bottom: 2px solid var(--vscode-tab-activeBorder);
}

.tab-icon {
  font-size: 12px;
}

.tab-name {
  font-size: 12px;
  flex: 1;
}

.tab-close {
  background: none;
  border: none;
  color: var(--vscode-tab-inactiveForeground);
  cursor: pointer;
  font-size: 14px;
  line-height: 1;
  padding: 0;
  width: 14px;
  height: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 2px;
  transition: background-color 0.2s, color 0.2s;
}

.tab-close:hover {
  background-color: var(--vscode-toolbar-hoverBackground);
  color: var(--vscode-tab-activeForeground);
}

.terminal-actions {
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 0 8px;
}

.new-terminal-dropdown {
  position: relative;
}

.new-terminal-dropdown:hover .dropdown-content {
  display: block;
}

.dropdown-content {
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  background: var(--vscode-dropdown-background);
  border: 1px solid var(--vscode-dropdown-border);
  border-radius: 4px;
  min-width: 120px;
  z-index: 1000;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.dropdown-content button {
  width: 100%;
  padding: 8px 12px;
  background: none;
  border: none;
  color: var(--vscode-dropdown-foreground);
  font-size: 12px;
  cursor: pointer;
  text-align: left;
  transition: background-color 0.2s;
}

.dropdown-content button:hover {
  background: var(--vscode-list-hoverBackground);
}

.runner-controls {
  display: flex;
  gap: 4px;
  margin: 0 8px;
  padding: 0 8px;
  border-left: 1px solid var(--vscode-panel-border);
  border-right: 1px solid var(--vscode-panel-border);
}

.action-btn,
.control-btn {
  background: none;
  border: none;
  color: var(--vscode-foreground);
  cursor: pointer;
  padding: 4px 6px;
  border-radius: 3px;
  font-size: 12px;
  transition: background-color 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 24px;
  height: 24px;
}

.action-btn:hover,
.control-btn:hover {
  background-color: var(--vscode-toolbar-hoverBackground);
}

.control-btn.active {
  background-color: var(--vscode-button-background);
  color: var(--vscode-button-foreground);
}

.control-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.terminal-content {
  flex: 1;
  overflow-y: auto;
  padding: 8px 12px;
  background-color: var(--vscode-terminal-background);
}

.terminal-content::-webkit-scrollbar {
  width: 8px;
}

.terminal-content::-webkit-scrollbar-track {
  background: var(--vscode-terminal-background);
}

.terminal-content::-webkit-scrollbar-thumb {
  background: var(--vscode-scrollbarSlider-background);
  border-radius: 4px;
}

.terminal-content::-webkit-scrollbar-thumb:hover {
  background: var(--vscode-scrollbarSlider-hoverBackground);
}

.terminal-messages {
  font-family: var(--vscode-editor-font-family);
  font-size: 13px;
  line-height: 1.4;
}

.terminal-message {
  display: flex;
  margin-bottom: 2px;
  word-wrap: break-word;
  white-space: pre-wrap;
}

.terminal-message.input {
  color: var(--vscode-terminal-foreground);
}

.terminal-message.output {
  color: var(--vscode-terminal-foreground);
}

.terminal-message.error {
  color: var(--vscode-errorForeground);
}

.terminal-message.system {
  color: var(--vscode-terminal-ansiBrightBlue);
  font-style: italic;
}

.message-timestamp {
  color: var(--vscode-descriptionForeground);
  font-size: 11px;
  margin-right: 8px;
  min-width: 60px;
  opacity: 0.7;
}

.message-content {
  flex: 1;
  word-break: break-all;
}

.terminal-input-line {
  display: flex;
  align-items: center;
  margin-top: 4px;
}

.terminal-prompt {
  color: var(--vscode-terminal-ansiGreen);
  font-weight: 600;
  margin-right: 4px;
  user-select: none;
}

.terminal-input {
  flex: 1;
  background: transparent;
  border: none;
  color: var(--vscode-terminal-foreground);
  font-family: var(--vscode-editor-font-family);
  font-size: 13px;
  outline: none;
  padding: 0;
  margin: 0;
  caret-color: var(--vscode-terminal-foreground);
}

.terminal-input::selection {
  background: var(--vscode-terminal-selectionBackground);
}

.terminal-status {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px 12px;
  background-color: var(--vscode-statusBar-background);
  color: var(--vscode-statusBar-foreground);
  border-top: 1px solid var(--vscode-statusBar-border);
  font-size: 11px;
}

.status-left,
.status-right {
  display: flex;
  align-items: center;
  gap: 12px;
}

.status-indicator {
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 11px;
}

.status-indicator.running {
  color: var(--vscode-terminal-ansiGreen);
}

.status-indicator.stopped {
  color: var(--vscode-terminal-ansiRed);
}

.status-indicator.error {
  color: var(--vscode-terminal-ansiYellow);
}

.status-indicator.building {
  color: var(--vscode-terminal-ansiBlue);
}

.workspace-info {
  color: var(--vscode-statusBar-foreground);
  opacity: 0.8;
}

.debug-test-toggle {
  background: none;
  border: none;
  color: var(--vscode-statusBar-foreground);
  cursor: pointer;
  padding: 4px 8px;
  border-radius: 3px;
  font-size: 11px;
  transition: background-color 0.2s;
  display: flex;
  align-items: center;
  gap: 4px;
}

.debug-test-toggle:hover {
  background-color: var(--vscode-statusBarItem-hoverBackground);
}

.debug-test-toggle.active {
  background-color: var(--vscode-button-background);
  color: var(--vscode-button-foreground);
}

.debug-test-panel-container {
  position: fixed;
  top: 60px;
  right: 20px;
  bottom: 60px;
  width: 450px;
  z-index: 1000;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  overflow: hidden;
  backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.95);
  border: 1px solid var(--vscode-panel-border);
}

@media (max-width: 1200px) {
  .debug-test-panel-container {
    width: 380px;
    right: 10px;
  }
}

@media (max-width: 768px) {
  .debug-test-panel-container {
    position: fixed;
    top: 50px;
    left: 10px;
    right: 10px;
    bottom: 50px;
    width: auto;
  }
}

/* Responsive */
@media (max-width: 768px) {
  .terminal-tab {
    min-width: 80px;
    padding: 4px 8px;
  }

  .tab-name {
    font-size: 11px;
  }

  .terminal-actions {
    gap: 2px;
    padding: 0 4px;
  }

  .runner-controls {
    margin: 0 4px;
    padding: 0 4px;
  }

  .terminal-content {
    padding: 6px 8px;
  }

  .terminal-messages {
    font-size: 12px;
  }

  .terminal-input {
    font-size: 12px;
  }

  .terminal-status {
    padding: 3px 8px;
    font-size: 10px;
  }

  .message-timestamp {
    display: none;
  }
}

/* Animation for building status */
@keyframes pulse {
  0% { opacity: 1; }
  50% { opacity: 0.5; }
  100% { opacity: 1; }
}

.status-indicator.building {
  animation: pulse 1.5s infinite;
}
